<template>
    <div class="user-info">
      <form @submit.prevent="submitUser">
        <div class="info-list">
          <div class="list">
            <div class="tit">头像<span></span></div>
            <div class="list-content">
              <van-uploader :after-read="onRead" accept="image/gif, image/jpeg" multiple>
              </van-uploader>
              <!-- <img class="head" :src="userInfo.photo" alt=""> -->
              <img class="head" :src="userInfo.photo" alt="">
              <span class="iconfont icon-yousanjiao1"></span>
            </div>
          </div>
          <div class="list">
            <div class="tit">姓名<span></span></div>
            <div class="list-content">
              <input type="text" required placeholder="请输入您的姓名" v-model="userInfo.nickname">
            </div>
          </div>
          <div class="list">
            <div class="tit">性别<span></span></div>
            <div class="list-content">
              <div class="ip_sex">
                <input  type="radio" value="2" v-model="userInfo.sex" id="female" name="sex">
                <label  for="female">女</label>
              </div>
              <div class="ip_sex">
                <input id="male" value="1" v-model="userInfo.sex" type="radio" name="sex">
                <label for="male">男</label>
              </div>
            </div>
          </div>
          <div class="list">
            <div class="tit">生日<span></span></div>
            <div class="list-content">
              <span @click="$refs.pickerone.open()">{{userInfo.birthday||'请选择'}}</span>
              <span class="iconfont icon-yousanjiao1"></span>
            </div>
          </div>
          <div class="list">
            <div class="tit">收货地址<span></span></div>
            <div class="list-content" @click="toAddress">
              <span>{{userInfo.has_address==0?'请选择':'点击查看'}}</span>
              <span class="iconfont icon-yousanjiao1"></span>
            </div>
          </div>
          <div class="list">
            <div class="tit">银行卡<span></span></div>
            <div class="list-content" @click="toCreditCard">
              <span>{{userInfo.has_card==0?'请添加银行卡':userInfo.card_name}}</span>
              <span class="iconfont icon-yousanjiao1"></span>
            </div>
          </div>
        </div>
        <van-button  native-type="submit" class="btn-login" type="default" plain>保存</van-button>
      </form>
      <mt-datetime-picker
        ref="pickerone"
        type="date"
        year-format="{value} 年"
        month-format="{value} 月"
        date-format="{value} 日"
        :startDate="new Date('1940/1/1')"
        :endDate="new Date()"
        @confirm="handleConfirm">
      </mt-datetime-picker>
    </div>
</template>

<script type="text/ecmascript-6">
import Vue from "vue";
import { Toast, Uploader  } from "vant"
Vue.use(Uploader);
import axios from 'axios'
import { apiPost, apiGet } from "../../common/js/api.js"
import { getStore } from "../../assets/utils.js"
import imgurl from "../../assets/img/huiyuanxinxi--touxiang.png"
export default {
    name: "user-info",
    
    data(){
      return{
        userInfo:{
          sex:'1',
          addressId:2,
          birthday:'',
          uptoken_url:'',
          accessToken:'',
          creditName:"",
          creditCard:0,
          photo:''
        }//用户信息
      }
    },
    components: { },
    computed: {},
    watch:{},
    beforeCreate(){

    },
    created(){
      if(getStore("chooseAdd")){
        this.userInfo.addressId = getStore("chooseAdd").addressId
        this.userInfo.addressName = getStore("chooseAdd").addressName
      }
      this.accessToken=this.$cookie.get('accessToken')
      this.getUserInfo();
      axios.get('http://anfuapi.demo.sclonsee.com/v1/index/uptoken').then((res)=>{
        this.uptoken_url=res.data.data.uptoken;
        console.log(this.uptoken_url)
      })
    },
    beforeMount(){

    },
    mounted(){

    },
    methods:{
      //获取用户信息
      getUserInfo(){
        apiPost("/account/myinfo",{accessToken:this.accessToken,type:1}).then(res=>{
          console.log(res)
          this.userInfo.sex=res.data.data.sex
            this.userInfo.addressId=res.data.data.addressId
            this.userInfo.birthday=res.data.data.birthday
            this.userInfo.photo=res.data.data.photo
          this.userInfo.nickname = res.data.data.nickname
          this.userInfo.has_address=res.data.data.has_address
          this.userInfo.has_card=res.data.data.has_card
          this.userInfo.card_name=res.data.data.card_name
          console.log(this.userInfo.photo)
        })
      },
      //选中图片
      onRead(file){
        let param = new FormData(); //创建form对象
        param.append('file',file.file);//通过append向form对象添加数据
        // param.append('chunk','0');//添加form表单中其他数据
        param.append('token',this.uptoken_url)
        console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
        let config = {
          headers:{'Content-Type':'multipart/form-data'}
        };  //添加请求头
        axios.post('http://up-z2.qiniu.com/',param,config)
          .then(response=>{
            this.userInfo.photo = `http://pbu0dcbw1.bkt.clouddn.com/${response.data.key}`
          })
      },
      //时间选择回调
      handleConfirm(date){
        this.userInfo.birthday=date.toLocaleDateString().replace(/\//g,"-");
        console.log( this.userInfo.birthday )
      /*  this.$set(this.userInfo,'birthday',date.toLocaleDateString().replace(/\//g,"-"))*/
      },
      //前往地址列表页
      toAddress(){
        const { addressId, addressName } = this.userInfo
        this.$router.push({
          path:'/addressList',
          query:{
            addressId,
            addressName
          }
        })
      },
      //前往银行卡列表页
      toCreditCard(){
        const { creditCardId, creditCardName } = this.userInfo
        this.$router.push({
          path:'/creditCardList',
        })
      },
      //保存用户信息
      submitUser(){
        if(this.userInfo.has_address==0 && this.userInfo.has_card==0){
          Toast('请尽快添加收货地址及银行卡!');
        }else if(this.userInfo.has_card==0){
          Toast('请尽快添加银行卡!');
        }
        else if(this.userInfo.has_address==0){
          Toast('请尽快添加收货地址!');
        }
        this.userInfo.type=2;
        this.userInfo.accessToken=this.accessToken;
        console.log(this.userInfo)
        apiPost('/account/myinfo',this.userInfo).then(res=>{
            console.log(res)
          if(res.data.status==200){
            Toast('修改成功!');
            this.$router.push('/userInfo');
          }else {
            Toast('修改失败,请重试!');
          }
        }).catch(err=>{

        })
      }
    }
}
</script>

<style scoped lang="scss">
  @import "./../../common/css/common.css";
  .user-info{
    background: #E6E9ED;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding:.18rem .13rem;
  }
  .info-list{
    background: #fff;
    border-radius: .04rem;
    width: 100%;
    padding:.05rem .04rem;
    margin:0 0 .73rem 0;
    .list{
      height:.5rem;
      font-size: .17rem;
      color: #9A9A9A;
      border-bottom: .01rem solid #F5F5F5;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding:0 .09rem;
      &:nth-last-child(1){
        border:0;
      }
      .tit{
        width: .68rem;
        height: 100%;
        line-height: .5rem;
        margin-right: .35rem;
        color: #333333;
        text-align: justify;
        &>span { 
          display: inline-block /* Opera */;
          padding-left: 100%; 
          // width:100%;
        }
      }
      .list-content{
        flex: 1;
        display: flex;
        justify-content: flex-end;
        height: 100%;
        align-items: center;
        color: #9A9A9A;
        .head{
          width: .31rem;
          height: .31rem;
          border-radius: 50%;
        }
        .icon-yousanjiao1{
          margin-left: .13rem;
        }
        &>input{
          text-align: right;
          border:0;
          height: 100%;
        }
      }
    }
  }
  .ip_sex{
    width: 0.7rem;
    float: right;
    color: #9a9a9a;
    display: flex;
    font-size: 0.17rem;
    justify-content: space-around;
    input{height: 0.5rem;width: 0.16rem}
  }
  .btn-login{
    width: 3.06rem;
    height: .47rem;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:0.04rem;
    font-size: 0.17rem;
    text-align: center;
    line-height: .47rem;
    outline: none;
    border: none;
    display: block;
    margin: 0 auto;
    color: white;
  }
</style>



